/*######-------- ||| ORDRE FLEXBOX ||| --------######*/
body .conteneur .conteneur-flex{
    #zone-1.offre-contrat-alternance{order:1;}
    #zone-2.offre-contrat-alternance{order:2;}
    #zone-3.offre-contrat-alternance{order:3;}
    #zone-4.offre-contrat-alternance{}
    #zone-5.offre-contrat-alternance{}
    @include breakpoint ($desktop){}
}

/*######-------- ||| BORDURES ||| --------######*/
body .conteneur .conteneur-flex,
body .conteneur{
    @include breakpoint(($desktop,$tablet)){
    }

}

/*######-------- ||| LAYOUT SPECIFIQUE ||| --------######*/
#zone-illustration.offre-contrat-alternance{
    min-height: 35vw;
    max-height: 90vw;
    overflow: hidden;
    @include backgroundImage('defaut_image_illustration_offre_ct.jpg');
    @include background-size(cover);    
    img{
        transform: translate(0%,0%);

    }
    @include breakpoint(($desktop,$desktopwide,$mobilelandscape,$tablet,$tabletlandscape)){
        min-height: 25vw;
        max-height: 30vw;
        img{
            transform: translate(0%,-15%);
        }
    }
}
#zone-1.offre-contrat-alternance{
    .content{
        @include breakpoint(($desktop,$desktopwide,$mobilelandscape,$tablet,$tabletlandscape)){
            padding: span(0.2);
        }
    }
       a.retour-liste{
        @include breakpoint(($desktop,$desktopwide,$mobilelandscape,$tablet,$tabletlandscape)){
        padding: span(0.2);
        display: block;
        }
    }
    .table-poste-bts,.table-offre-apprentissage{
        @include breakpoint(($desktop,$desktopwide,$mobilelandscape,$tablet,$tabletlandscape)){
            margin: span(0.1) auto;
            width: span(4);
        }
    }
}
#zone-2.offre-contrat-alternance{
    ul.liste-autre-offres {
        @include breakpoint(($desktop,$desktopwide,$mobilelandscape,$tablet,$tabletlandscape)){
            width: 95%;
            margin: span(0.01) auto;
            display: inline-block;
            li{
                a{
                    float: left;
                }
            }
        }
    }
}

/*######-------- ||| STYLE ||| --------######*/

/* STYLES COMMUNS */
#zone-1.offre-contrat-alternance,
#zone-2.offre-contrat-alternance,
#zone-3.offre-contrat-alternance,
#zone-4.offre-contrat-alternance,
#zone-5.offre-contrat-alternance
{
    p,ul,li{
        @include font-size(m);//utilisation de typey
        padding: em(5px);
        line-height: 1.5em;
    }
    u{
        font-weight: bold;
        color: $bleu-turquoise;
    }
    h2{
        @include font-size(l);//utilisation de typey
        color: $orange-fonce;
    }
    h3{
        @include font-size(m);//utilisation de typey
        color: $orange-fonce;
    }
    a{
        text-decoration: none;
        color: $orange-fonce;
        &:hover{
            text-decoration: underline;
        }
    }
    strong{
        color: $orange-fonce;
    }
}

/*------------------------------------
         STYLES PROPRES 
______________________________*/

#zone-1.offre-contrat-alternance{

    .table-poste-bts,.table-offre-apprentissage{

        tbody{
            border: 0;
            @include ombrage-table-actu;  
        }
        tr.line1.even{
            color: $violet;
            background: white;
        }
        tr.line2.odd{
            background: $gris-clair;
            color: $rose-fushia;
        }
        tr.postuler{
            background: $orange-fonce;
            color: $gris-clair;
            cursor:pointer;
            text-align: center;
            &:hover{
            background: $gris-anthracite;
            color: $gris-clair;    
            }
                 a{
            color: $gris-clair;
                 }
            
            }
        td.etat{
            font-weight: bold;
        }
        
        
        td{
            @include txt-cesure;
        }
        a{
            color: $orange-fonce;
            text-decoration: none;font-weight: bold;
          &:hover{text-decoration: underline;}
        }

    }
}
#zone-2.offre-contrat-alternance{
        h3.block-title{
           transform: rotate(-1deg);
        color: $orange-fonce;
        background: $gris-anthracite;
        width: 70%;
        width: fit-content;
        margin: span(0.1) auto;
    }
    ul.liste-autre-offres {


        background: $gris-clair;
        li{
            list-style: none;
        }
        h4{
            @include font-size(xs);//utilisation de typey
        }
        .ref-offre-autre{float: none;
                         @include font-size(xs);//utilisation de typey

        }
        a{
            margin: 0 span(0.1) 0 0;
            text-decoration: none;
            color: $violet;

            padding: em(3px);
            &:hover{text-decoration: underline;}
        }
    }

}
